}

.loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #ffffff;
  background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  animation: load3 1.4s infinite linear;
  transform: translateZ(0);
}

.loader:before {
  width: 50px;
  height: 50px;
  background: #ffffff;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}

.loader:after {
  /* background: #0dc5c1; */
  width: 50px;
  height: 50px;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

@keyframes load3 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #8248e5;
  border-top: 2px solid #8248e5;
  color:#8248e5;
}

.table td, .table th{
  vertical-align: bottom;
  border-bottom: 1px solid #8248e5;
  border-top: 1px solid #8248e5;
  color:#fff;
}

